(function() {
    'use strict';

    angular
        .module('app', []);

})();

(function() {
    'use strict';

    angular
        .module('app')
        .run(runBlock);

    /** @ngInject */
    function runBlock($rootScope, $state,$http){

    }

})();
(function() {
    'use strict';

    angular
        .module('app')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {
        $stateProvider
            .state('news', {
                url: '/news',
                data: {
                    displayName: 'News'
                },
                views: {
                    "@": {
                        templateUrl: 'app/components/news/news.html',
                        controller: 'NewsController',
                        controllerAs: 'news'
                    }
                }
            })
            .state('analytics', {
                url: '/analytics',
                data: {
                    displayName: 'Analytics'
                },
                views: {
                    "@": {
                        templateUrl: 'app/components/analytics/analytics.html',
                        controller: 'AnalyticsController',
                        controllerAs: 'analytics'
                    }
                }
            })
        ;

        $urlRouterProvider.otherwise('/news');
    }

})();

(function() {
    'use strict';

    angular.module('app')
        .config(config);

    function config($logProvider) {
        $logProvider.debugEnabled(true);
    }

})();
